// Promisepolyfill
require('es6-promise').polyfill();

import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import mutations from './mutations'

/***
 * @desc vuex
 * @author Lesty
 * @codeDate 2017.3.16
 */

Vue.use(Vuex);

const state = {
    // 
    linkHistory: [],
    // 
    reserveList: [],
    // pager
    reservePager: null,
    // 
    modifyFieldsObj: {},
    // ()
    initFieldsMap: {},
    // (key-value)
    originFieldsMap: {}
};

export default new Vuex.Store({
  state,
  actions,
  mutations
})



